ul.ps-2.pt-2 li.entry-root.DViewPoint.selected {
  position: relative;
  z-index: 0;

  &>.inline-row{
    background: #ddffdd!important;
    // background: #00ff00;
    border: 1px solid #ddffdd;
  }
  &>.inline-row:hover {
    border: 1px solid var(--bg-4);
  }


  // padding: 0.5em; padding-right: 0;
  // position: relative;
}





ul.ps-2.pt-2 .entry-root.DViewPoint.selected::after {
  position:absolute;
  left    : 4px;
  bottom  : 4px;
  display: block;
  border-left: 1px dotted var(--color);
  height: calc(100% - 2.8em);
  width: 4px;
  content: "";

}





.inline-row{
  display: flex;
  border: 1px solid transparent;
  padding-bottom: 1px;
}

.icon.type{
  // border: none;
  margin: auto;
  margin-left: 0.25em;
  margin-right: 0.25em;
  width: 2ic;
  height: 2ic;
  align-content: center;
  border-radius: 100%;
}
ul{
  list-style-type: none;
  padding-left: 1.5em;
}



.tree-Any {
  background-color: #5F0F40;
}
.tree-DEdgePoint, .tree-EdgePoint{
  background-color: #007700;
}
.tree-DEdge, .tree-Edge {
  background-color: #00aa00;
}
.tree-DField, .tree-Field {
  background-color: #aaa;
}
.tree-DVertex, .tree-Vertex {
  background-color: #9F4F80;
}
.tree-DGraphVertex, .tree-GraphVertex {
  background-color: #7F2F60;
}
.tree-DGraph, .tree-Graph {
  background-color: #5F0F40;
}
