.vertical-centering{
  position: relative;
  display: flex;
  >*{
    margin-top: auto;
    margin-bottom: auto;
  }
}

.right-icon{
  display: inline-block;
  border-radius: calc(var(--radius) - 1px);
  padding-top: 2px;
  margin-left: 2px;
  height: 24px;
  transition: background-color 0.3s;
  &.hidden{
    opacity: 0.33;
    color: #333;
    outline-color: #333;
    text-shadow: none;
  }
}

.viewpoint-tab .entry-root.DViewPoint .hover-stuff .bi{
  &::before {
    border: none;
    color: inherit;
    background-color: var(--btn-bg);
    border-radius: 5px;
  }
  &:hover::before {
    outline: 1px solid black;
  }
  &.disabled:hover::before, &:hover:disabled::before{
    outline: none;
  }
}

.right-icon:hover {
  cursor: pointer;
  color: white;
}

.ocl-icon {
  transition: background-color 0.3s;
  color: white;
}
.ocl-icon::before {
  opacity: 0.7;
  position: relative;
  display: block;
  width: 1.7rem;
  height: 22px;
  border: 1px solid var(--failure);
  background-color: var(--failure);
  color: var(--white)!important;
  border-radius: var(--radius);
  text-align: center;
  margin: auto;
  padding-left: auto;
  padding-right: auto;
  padding-top: 2px;
  font-weight: 600;
  font-size: 0.85em;

  content: "OCL";

}
.ocl-icon:hover::before {
  opacity: 1;
}

.js-icon {
  transition: background-color 0.3s;
  color: white;
}

.js-icon::before {
  opacity: 0.7;
  position: relative;
  padding-top: 2px;
  display: block;
  min-width: 1.7rem;
  height: 22px;
  border: 1px solid var(--accent-secondary);
  background-color: var(--accent-secondary);
  color: var(--white)!important;
  border-radius: var(--radius);
  text-align: center;
  padding-left: auto;
  padding-right: auto;
  margin: auto;
  font-weight: 600;
  font-size: 0.85em;

  content: "JS";
}
.js-icon:hover::before {
  opacity: 1;
}

.ex-icon {
  transition: background-color 0.3s;
  color: white;
}
.ex-icon::before {


  opacity: 0.7;
  position: relative;
  display: block;
  width: 1.7rem;
  height: 22px;
  border: 1px solid var(--accent);
  background-color: var(--accent);
  color: var(--white)!important;
  border-radius: var(--radius);
  text-align: center;
  margin: auto;
  padding-left: auto;
  padding-right: auto;
  padding-top: 2px;
  font-weight: 600;
  font-size: 0.85em;

  content: "EX";
  top: 0!important;

}
.ex-icon:hover::before {
  opacity: 1;
}

.right-content, .hover-stuff{
  cursor: pointer;
  display: flex;
  height: 26px;
}

.left-stuff{
  padding-left: 0.125em;
  .bi{
    font-size: 0.7em;
    color: gray;
    cursor: pointer;
  }
  .bi-caret-right-fill{
    // visibility: hidden;
    cursor: not-allowed;
  }
  .bi:hover{
    color: black;
  }
  .bi-chevron-down::before, .bi-chevron-right::before {
    scale: 1.25;
    font-weight: 900 !important;
  }
  width: 1.3ch;
}
.right-content{
  cursor: default;

  & .priority {
    display: block;
    min-width: 6rem;
    height: 22px;
    border: 1px solid var(--color);
    border-radius: var(--radius);
    margin: auto;
    text-align: center;
    padding-left: 10px;
    padding-right: 10px;

    width: 72px;
    white-space: nowrap;

  }
  & .priority-booster {
    display: block;
    height: 24px;

    margin: auto;
    margin-right: 10px;
    text-align: center;
    padding-left: 10px;
    padding-right: 10px;

    width: 1.5em;
    height: 22px;
    border: 1px solid var(--color)!important;
    border-radius: var(--radius);

    text-align: center;
    &:hover, &:focus{
      outline: 2px solid black;
    }
  }
}
.right-stuff{
  max-height: 26px;
  height: 26px;
  .spacer{
    margin-left: auto;
  }
  width: fit-content;
}
.inline-row {
  position: relative;
  padding-right: 0.5em;
  border-radius: 10px;
  left: -10px;
  padding-left: 10px;

  &:hover{
    background-color: var(--bg-3);
    outline: none;
    border-radius: var(--radius);
    padding-left: 10px;

  }
  .right-stuff{
    transform-origin:right;
  }
  .hover-stuff{
    height: 26px;
    &>*{ height: 26px; }
    .bg, .bx{
      height: 26px;
      font-size: 15px;
    }

    .bx{
      display: flex;
      font-size: 16px;
      &:before{
        line-height: 1;
        margin: auto;
      }
    }
    //transform: scaleX(0);
    transition: var(--anim-out);
    // position: absolute;
    right: 0;
    transform: scaleX(0);
    transform-origin: right;

    // float: right;
    margin-right: 0.5em;
  }
  &:hover{
    .hover-stuff{
      transition: var(--anim-in);
      transform: scaleX(1);
    }
    .right-content{/*
      width: 0;
      //transform: scaleX(0);
      transition: var(--anim-out);
      transform: scaleX(0);*/
    }
  }
}


.btn-delete.green:hover{
  background: var(--palette-gg);
}
.mid-stuff{
  flex-grow:1;
  cursor: pointer;
}
.single-view-content{
  position: absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  background-color: var(--bg-color);
  z-index: 1;
}

.view-editor-root {
  height: 100%;
  max-height: 100%;
  overflow: hidden;
  padding-right: 0.25em;
  background: white;

  &:has(.single-view-content){
    overflow: hidden;
  }
  .dock-content .dock-tabpane{
    overflow: auto !important;
  }

  section.viewpoint-tab {

    color: var(--color);
    margin: 10px; // var(--tab-margin);
    padding: 8px;
    font-weight: 400;

    border: 1px solid var(--bg-1-5);
    border-radius: var(--radius);
    background-color: var(--bg-1-5);
    transition: opacity 0.3s;
  }

  section.viewpoint-tab .ps-2.pt-2 h1 {
    width: 100%!important;
    display: block;
    clear: right!important;
    font-size: 1.1em;
    color: var(--accent);
    border-bottom: 1px solid var(--bg-2-5);



  }

  section.viewpoint-tab h1::before {
    position: relative;

    font-size: 1.15em;
    margin-right: var(--tab-sep);
    font-family: bootstrap-icons;
    content: "\F6D2";
  }

  .viewpoint {

  }
}

.view-editor-root{
  --inner-page-padding: 1rem;
  display: flex;
  flex-flow: column;

  .btn-back{
    font-size: 0.9em;
    margin: auto;
    &i::before{
      transform: translate(0, 1px);
    }
  }
  .dock-layout{
    flex-grow: 1;
    .dock-nav-wrap{
      margin-left: 0.25rem;
    }
    .dock-panel-max-btn{
      display: none;
    }
  }
  .page-root{
    padding: var(--inner-page-padding);
    padding-top: 0;
  }
  .view-editor-header {
    margin-top: 0.25em;
    margin-left: var(--inner-page-padding);
    margin-right: var(--inner-page-padding);
    display: inline-flex;

    .path-list{
      display: inline-flex;
      // border: 2px solid red;
      // &>*{ border: 2px solid black; }
      margin: auto;
      margin-left: 0.5em;
      padding: 0px 0px 4px 0px;
      margin-bottom: 10px!important;

      .path-separator{
        height: min-content;
        margin: auto;
        margin-left: 0.125em;
        margin-right: 0.125em;
      }
      .path-element {
        cursor: pointer;
        margin: auto;
        &:hover{
          //font-weight: 900;
          text-decoration: underline;
        }
      }
    }
  }


  .editor-label {
    margin: auto;
    margin-left: 0.5rem;
    margin-top: 10px;
    margin-bottom: 10px;
    & input {

    }
  }

  .chevron-holder {
    margin-top: 10px;
  }

  .view-editor-header {
    margin-top: 26px!important;
    margin-bottom: 6px;
  }

  .path-list {
    float: right!important;
  }
}


